
import couponDialog from "../../components/couponDialog/couponDialog.vue";
import { commoditydecApi } from "./conf/api";
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Previewer from 'vux/src/components/previewer/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

export default {
directives: {
TransferDom
},
components: { Swiper, SwiperItem, Previewer, couponDialog },
data() {
return {
id: "",
loadingEnd: false,
commoditydec: {},
commodityList: [],
photoPreviewList: []
};
},

methods: {
onSwiperItemClick(index) {
this.$refs.previewer.show(index);
},

onCommodityItemClick(item) {

sessionStorage.setItem("commoditydec", JSON.stringify(item));
this.commoditydec = item;
this.updateSwipeList();
this.requestGetSimilarGoods(item.NumIid);
},

getCoupon(item) {
this.requestUTbTpwdCreate(item);
},

updateSwipeList() {

this.photoPreviewList = [];

var photopreview = {};
photopreview.src = this.commoditydec.PictUrl;
this.photoPreviewList.push(photopreview);

for (var i = 0; i < this.commoditydec.SmallImages.length; i++) {
var photopreview = {};
photopreview.src = this.commoditydec.SmallImages[i];
this.photoPreviewList.push(photopreview);
}

window.scrollTo(0, 0);
},

requestGetSimilarGoods(id) {
this.$request
.loading(
this,
commoditydecApi.GetSimilarGoods,
"get",
{
PromoterId: "",
id: id
},
""
)
.then(
success => {
this.loadingEnd = true;

this.commodityList = success.data.data;
},
fial => {
this.loadingEnd = true;
}
);
},

requestUTbTpwdCreate(item) {
this.$request
.loading(
this,
commoditydecApi.UTbTpwdCreate,
"post",
{
ConvertTypes: 0,
Logo: item.PictUrl,
Text: item.Title,
Url: item.CouponClickUrl,
NumIid: item.NumIid,
Title: item.Title,
PictUrl: item.PictUrl,
ZkFinalPrice: item.ZkFinalPrice,
CouponInfo: item.CouponInfo,
CouponFinalPrice: item.CouponFinalPrice
},
""
)
.then(
success => {
var title = item.Title + "<br/>";
var oldPrice = " " + item.ZkFinalPrice + "<br/>";
var newPrice = " " + item.CouponFinalPrice + "<br/>";
var shortlink =
"" + success.data.data.ShortUrl + "<br/>";
var kl =
"" +
success.data.data.Kl +
"";
this.$refs.couponDialog.openDialog(
title + oldPrice + newPrice + shortlink + kl
);
},
fail => {}
);
}
},
beforeRouteEnter(to, from, next) {
next(vm => {
if (from.name == "index" || from.name == "commodityList") {

sessionStorage.setItem("refresh", "false");
sessionStorage.setItem("commoditydec", JSON.stringify(to.params.item));

vm.commoditydec = to.params.item;
vm.updateSwipeList();
vm.requestGetSimilarGoods(vm.commoditydec.NumIid);
} else if (from.name == null) {
sessionStorage.setItem("refresh", "true");

vm.commoditydec = JSON.parse(sessionStorage.getItem("commoditydec"));
vm.updateSwipeList();
vm.requestGetSimilarGoods(vm.commoditydec.NumIid);
}
});
}
};



