export default {
  components: {},
  data() {
    return {
      couponCentent: "",
      isShowCouponDialog: false
    };
  },
  //
  methods: {
    // 
    onCopySuccess(res) {
      this.isShowCouponDialog = false;
      this.$vux.toast.text("", "middle");
    },
    // 
    onCopyError(res) {
      this.$vux.toast.text("", "middle");
    },
    // 
    openDialog(couponCentent) {
      // 
      this.couponCentent = couponCentent;
      this.isShowCouponDialog = true;
    },
    // 
    closeDialog() {
      this.isShowCouponDialog = false;
    }
  },
  mounted() {}
};



